<template>
  <div class="shujjc">
      <!-- <el-empty v-if="tableData = undefined" description="暂无用户"></el-empty> -->
      <el-table
          :data="pm"
          stripe
          style="width: 100%">
          <el-table-column
          align="center"
          prop="id"
          label="ID"
          width="180">
          </el-table-column>
          <el-table-column
          align="center"
          prop="password"
          label="密码">
          </el-table-column>

          <el-table-column label="密码" align="center">
            <template slot-scope="scope">
              <el-input
                v-if="isshow[scope.$index]"
                type="text"
                size="mini"
                clearable
                v-model="edg[scope.$index]"
              />
              <span v-else style="margin-left: 10px">{{ scope.row.password }}</span>
            </template>
          </el-table-column>

          <el-table-column label="操作" align="center">
              <template slot-scope="scope">
                  <el-button
                  size="mini"
                  v-if="!isshow[scope.$index]"
                  @click="handleEdit(scope.$index, scope.row)"><i class="el-icon-edit"></i>编辑</el-button>
                  <el-button
                    v-if="isshow[scope.$index]"
                    size="mini"
                    type="success"
                    @click="handelCheck(scope.$index,scope.row)"
                  ><i class="el-icon-check"></i>保存</el-button>
                  <el-button
                    v-if="isshow[scope.$index]"
                    size="mini"
                    type="success"
                    @click="handelQx(scope.$index,scope.row)"
                  ><i class="el-icon-close"></i>取消</el-button>
                  <el-button
                  size="mini"
                  type="danger"
                  @click="handleDelete(scope.$index, scope.row)"><i class="el-icon-delete"></i>删除</el-button>
              </template>
          </el-table-column>
      </el-table>

      <el-pagination
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      :current-page="page"
      :page-sizes="[6]"
      :page-size="6"
      layout="total, sizes, prev, pager, next, jumper"
      :total="total">
      </el-pagination>
  </div>
</template>

<script>

export default {
  data(){
      return{
          tdp: [],
          // tableData:[[]],
          pm:[],
          page:1,
          total:null,
          ed:false,
          isshow: [],
          edg:[],
      }
  },
  methods:{
      handleEdit(index, row) {
          console.log(index, row);
          // this.ed = true
          // this.edg=row.id
          this.$set(this.isshow, index, true);
          this.$set(this.edg,index,row.password)
      },
      handelCheck(index,row) {
        this.$set(this.isshow, index, false);
        // this.$set(row.id,index,this.edg[index])
        row.password=this.edg[index];
        this.$api.geteduser({
          id:row.id,password:this.edg[index],cid:row.id
        }).then(res=>{})
      },
      handelQx(index,row){
        this.$set(this.isshow, index, false);
      },
      handleDelete(index, row) {
          console.log(index, row);
          this.pm.splice(index,1);
          this.$api.getdeluser({id : row.id}).then(res=>{
            console.log(res.data.info);
          })
          this.apidate();
          // this.getd()
      },
      handleSizeChange(val) {
          console.log(`每页 ${val} 条`);
      },
      handleCurrentChange(val) {
          console.log(`当前页: ${val}`);
          this.page=val;
          sessionStorage.setItem('page',val)
          this.apidate()
      },
      apidate(){
        this.$api.getuser({page:this.page}).then(res=>{
          // console.log(res.data.data);
          this.pm=res.data.data
          this.total=res.data.total
        })
      }
  },
  mounted(){
      if(sessionStorage.getItem('page')){
          this.page=Number(sessionStorage.getItem('page'));
      }
    // this.getd()
    this.apidate()
  },
  created(){
      // this.getd()
  }
}
</script>

<style lang="less" scoped>
  .shujjc{
      margin-top: 70px;
      margin-right: 10px;
  }
</style>

  